html {
    background-color: darkgray;
}

/* Offset for navbar */
.container {
    margin: 7vh auto auto;
}

#navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: fixed;
    top: 0;
    min-height: 7vh;
    width: 100%;

    background-color: #282c34;
}

#navbar a {
    margin: auto 10px;
    padding: 4px 8px;
    border-radius: 5px;

    text-decoration: none;
    color: darkgrey;
    font-size: 24px;
    font-weight: bold;
    transition: .25s;
}

#navbar a.active {
    color: aliceblue;
}

#navbar a:hover {
    background-color: black;
}

#themes {
    margin: 12px;
}

#theme {
    padding: 4px;
    border-radius: 5px;

    background-color: #23252d;
    border: none;
    color: aliceblue;
    font-size: 16px;
}

#projects {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: space-around;
}

.col {
    width: 50%;
}

.tile {
    display: flex;

    margin: 10px;

    border: none;
    background-color: rgba(0,0,0,0);
    transition: .25s;
    text-decoration: none;
    color: black;
}

.tile:hover {
    transform: scale(1.05);
}

.tile .image {
    flex: 2;

    background-color: white;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
}

.tile .image img {
    width: 100%;
    vertical-align: top;

    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    border: 1px solid grey;
}

.tile .content {
    display: flex;
    flex-direction: column;
    flex: 1;

    padding-left: 10px;

    background-color: white;
    border: 1px solid grey;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
}

.tile .content .title {
    flex: initial;

    padding: .3vh 0.25vh 0.25vh;

    text-align: left;
    font-size: 24px;
    font-weight: bolder;
    letter-spacing: 0.05rem; /*TODO*/
}

.tile .content .subtitle {
    flex: initial;

    padding: 0.25vh;

    text-align: left;
    font-size: 16px;
}

.tile .content .description {
    flex: auto;

    padding: 0.25vh;

    text-align: left;
    font-size: 12px;
}